.SubHeader {
  padding: 2rem;
  position: relative;
  height: 20rem;
  background: #393939;
  overflow: hidden;
  display: flex;
  align-items: center;
  margin: -2rem -10rem 2rem;
  @media screen and (max-width: 1000px) {
    padding-left: 2rem;
    margin: -2rem -10rem 2rem -4rem;
  }
  @media screen and (max-width: 600px) {
    margin-right: -2rem;
  }
  .picto {
    --size: 20rem;
    width: var(--size);
    height: var(--size);
    fill: #8d8d8d;
    opacity: 0.1;
    position: absolute;
    top: 0rem;
    transition: transform 0.05s ease-out;
    transform: translateY(calc(var(--scrollTop) * -0.25px));
    @media screen and (max-width: 1000px) {
      left: -10rem;
    }
    animation: fadeInLeft 1s ease-in-out;
    @keyframes fadeInLeft {
      from {
        transform: translateX(-20%);
        opacity: 0;
      }
    }
  }
  h2 {
    padding: 0 8rem;
    @media screen and (max-width: 1000px) {
      padding-left: 2rem;
    }
    position: relative;
    z-index: 2;
    font-size: 4rem;
    @media screen and (max-width: 650px) {
      font-size: 3rem;
      padding-right: 4rem;
      max-width: 100%;
      display: flex;
      flex-direction: column;
    }
    text-shadow: 0px 1px 0px rgba(0, 0, 0, 0.005);
    > svg {
      animation: ArrowAnim 1.5s linear infinite;
    }
  }
}

@keyframes ArrowAnim {
  0% {
    transform: translateX(-25%);
    opacity: 0;
  }
  10% {
    opacity: 0.5;
  }
  40% {
    transform: translateX(0%);
    opacity: 0.5;
  }
  80% {
    opacity: 0.5;
  }
  90% {
    transform: translateX(25%);
    opacity: 0;
  }
  100% {
    transform: translateX(25%);
    opacity: 0;
  }
}
